
*{
  margin: 0;
  padding: 0;
}
body {
  overflow-x: hidden!important;
}
.slide-left-enter,
.slide-right-leave-active {
  -webkit-transform: translate(800px, 0);
          transform: translate(800px, 0);
  transition: 0.6s;
}
.slide-left-leave-active,
.slide-right-enter {
  -webkit-transform: translate(-800px, 0);
          transform: translate(-800px, 0);
  transition: 0.6s;
}
.layout .main-content {
  padding-top: 60px;
  max-width: 800px;
  margin: 0 auto;
}
.layout .menu {
  box-shadow: 0 3px 6px #aaa;
  position: fixed;
  width: 100%;
  z-index: 10;
}
.layout .menu-title {
  font-size: 24px;
}



